.Home {
	width: 100%;
	display: flex;
	flex-direction: column;
	margin: auto;
}
.Home__hero {
	display: flex;
	gap: 11%;
	background: white;
	padding: 0 19%;
}
.Home__introduction {
	width: 445px;
	display: flex;
	flex-direction: column;
	gap: 1.5vw;
	float: left;
	padding: 0 1%;
	margin: 11% 0.3%;
}
.Home__hero_title {
	color: rgb(76, 88, 102);
}
.Home__title {
	font-size: 6.6vh;
	font-weight: 400;
	line-height: 100%;
}
.Home__description {
	color: rgb(133, 133, 155);
	font-size: 2.2vh;
	line-height: 150%;
}
.Home__hero_img {
	width: 50%;
	margin: 5% 0 0 0;
}
.Home__img {
	width: 100%;
	height: 100%;
}
.Home__mw {
	background: rgb(235, 235, 235);
	padding: 5vw 19vw;
	width: 100%;

	display: flex;
	gap: 10vw;
	flex-direction: column;
}
.Home__mw_result {
	display: flex;
	flex-direction: row;
	margin: auto;
	gap: 4vw;
}
.Home__mw_result_card {
	padding: 2% 0 0 0;
	display: flex;
	gap: 1.6vw;
	border-top: solid rgb(192, 192, 192);
}
.Home__card_desc {
	display: flex;
	width: 8.6vw;
	flex-direction: column;
	padding: 2vh 0;
}
.Home__card_title {
	color: rgb(102, 102, 102);
	font-size: 2.2vh;
	font-weight: 500;
}
.Home__card_text {
	color: rgb(133, 133, 155);
	font-size: 2vh;
	font-weight: 400;
}
.Home__card_img {
	width: 8.6vw;
}
.Home__mw_info {
	display: flex;
	gap: 1.6vw;
	flex-direction: row;
}
.Home__mw_list {
	width: 40vw;
	height: 61vh;
}
.Home__mw_img {
	width: 40vw;
	height: 61vh;
}
.Home__mw_title {
	color: rgb(102, 102, 102);
	font-size: 4.4vh;
	font-weight: 400;
}
.Home__mw_text {
	color: rgb(133, 133, 155);
	font-size: 2.06vh;
	font-weight: 400;
	line-height: 170%;
}
.Home__goods {
	background: rgb(255, 255, 255);
	padding: 5vw 19vw;
	width: 100%;

	display: flex;
	gap: 1.5vw;
	flex-direction: row;
}
.Home__goods_card {
	width: 13.7vw;
	display: flex;
	flex-direction: column;
	gap: 2.2vh;
}
.Home__goods_desc {
	display: flex;
	flex-direction: column;
	gap: 0.88vh;
}
.Home__goods_title {
	color: rgb(102, 102, 102);
	font-size: 3.3vh;
	font-weight: 400;
	line-height: 3.5vh;
}
.Home__goods_circ {
	color: rgb(181, 181, 189);
	font-size: 1.5vh;
	font-weight: 400;
}
.Home__goods_img {
	width: 100%;
}
.Home__goods_text {
	color: rgb(133, 133, 155);
	font-size: 2vh;
	font-weight: 400;
	line-height: 170%;
}
.Home__spons {
	background: rgb(235, 235, 235);
	margin: auto;
	padding: 5vw 18%;
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: auto;
}
.Home__spons_carts {
	display: flex;
	flex-direction: row;
	width: 100%;
	gap: 1.7vw;
}
.Home__spons_img {
	width: 12vw;
}
.Home__spons_cart {
	padding: 2% 2%;
	border: 1px solid rgb(196, 196, 196);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.Home__sketchbook {
	background: rgb(255, 255, 255);
	padding: 5vw 19vw 0;
	display: flex;
	gap: 1.5vw;
	flex-direction: row;
}
.Home__sketchbook_content {
	display: flex;
	gap: 1.5vw;
	flex-direction: row;
}
.Home__sketchbook_block {
	display: flex;
	flex-direction: column;
	gap: 1.5vw;
}
.Home__sketchbook_info {
	display: flex;
	flex-direction: column;
	gap: 1vh;
}
.Home__sketchbook_title {
	color: rgb(102, 102, 102);
	font-size: 4.4vh;
	font-weight: 400;
}
.Home__sketchbook_text {
	color: rgb(133, 133, 155);
	font-size: 2vh;
	font-weight: 400;
	line-height: 170%;
}
.Home__sketchbook_img {
	width: 40vw;
	height: 50vh;
}
